.home{
    position: absolute;
    top: 50%;
    left: 50%;
    width: 400px;
    padding: 40px;
    transform: translate(-50%, -50%);
    background: rgba(66, 64, 64, 0.5);
    box-sizing: border-box;
    box-shadow: 0 15px 25px rgba(34, 8, 8, 0.699);
    border-radius: 10px;
    text-align: center;
}
h1,p {
    margin: 0 0 30px;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: -0.2px;
    color: rgba(255, 255, 255, 0.534);
  text-shadow: 0px 0px 0px;
}

h2{
    margin-top: 15px;
    padding-bottom: 25px;
}

.login_button {
    position: relative;
  display: inline-block;
  padding: 10px 20px;
  /* color: #03e9f4; */
  font-size: 16px;
  text-decoration: none;
  text-transform: uppercase;
  overflow: hidden;
  transition: .5s;
  margin-top: 40px;
  letter-spacing: 4px
}

.login_button:hover {
    background: #03e9f4;
  color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 5px #03e9f4,
              0 0 25px #03e9f4,
              0 0 50px #03e9f4,
              0 0 100px #03e9f4;
}

.navbar {
   background-color: tomato;
   display: flex;
   width: 100%;
   max-height: 82px;
   
}

.nav_header {
  float: left;
  margin: 8px;
  margin-left: 10px;
  margin-right: 10px;

  
}

.blog_search {
  width: 100%;
  margin: 8px;
  margin-left: 50px;
   
 }  


.blog_search input {
  width: 80%;
  padding: 10px 0;
  font-size: 16px;
  color: #fff;
  margin-bottom: 30px;
  border: none;
  border-bottom: 1px solid #fff;
  outline: none;
  background: transparent;
}

.submit {
  /* position: relative; */
  display: inline-block;
  padding: 10px 20px;
  /* color: #03e9f4; */
  font-size: 16px;
  text-decoration: none;
  text-transform: uppercase;
  /* overflow: hidden; */
  /* transition: .5s; */
  /* margin-top: 40px; */
  letter-spacing: 4px
}

.nav_user_data {
  display: flex;
  margin: 8px;
}

.signedIn {
  margin-left: 15px;
}

.logout_button {
  letter-spacing: 10px;
   
  max-height: 35px; 
  margin-left: 30px;

}

.notSignedIn {
  margin-top: 10px;
    margin-left: 1000px;
}

.avatar {
   border-radius: 4px;
}

.blogjs {
  padding: 30px 170px;

}

.blog_header {
  letter-spacing: -1px;
  font-size: 35px;
}

.blogs {
  display: grid;
  grid-template-columns: repeat(3,1fr);
  grid: gap 150px;
  padding: 80px 0;
  column-gap: 10px;
  
}

.blog {
  min-width: 305px;
  margin: auto;
  box-shadow: 0px 0px 50px black;
  height: 100%;
  text-decoration: none;
  color: black;
}

.blog > div {
  margin-top: 15px;
  display: flex;
  flex-direction: column;
  padding: 0px 30px 0px 30px;
}

.blog > img {
  width: 100%;
}

.sourceName > span {
  display: inline;
  background: #f44336;
  font-size: 11px;
  padding: 5px 6px;
  color: black;
  border-radius: 3px;
}

.sourceName > p {
  font-size: 10px;
  font-weight: 500;
  padding-left: 10px;
  padding-top: 25px;
}

.sourceName {
  display: flex;
  align-items: center;
}

.blog > div > h1 {
  font-size: 25px;
  letter-spacing: -0.6px;
  line-height: 1.3em;
  padding: 10px 0;
  font-weight: 700px;
  text-decoration: underline;
}

.blog > div > p {
  font-size: 13px;
  line-height: 1.6em;
  color: aliceblue;
  padding-bottom: 30px;
}

.no_blogs {
  font-size: 25px;
  font-weight: 500px;
  line-height: 1.5em;
}
